import React, { Component } from 'react';
import './Profile.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';

class Profile extends Component {
  render() {
    return (     
   <form action="">
     <div className="form-group row">
      <label for="username" className="col-4 col-form-label"> *</label> 
      <div className="col-8">
        <input id="username" name="username" placeholder=" " className="form-control here" required="required" type="text" />
      </div>
    </div>
    <div className="form-group row">
      <label for="name" className="col-4 col-form-label"> </label> 
      <div className="col-8">
        <input id="name" name="name" placeholder=" " className="form-control here" type="text" />
      </div>
    </div>
    <div className="form-group row">
      <label for="lastname" className="col-4 col-form-label"> </label> 
      <div className="col-8">
        <input id="lastname" name="lastname" placeholder=" " className="form-control here" type="text"/>
      </div>
    </div>
    <div className="form-group row">
      <label for="text" className="col-4 col-form-label">*</label> 
      <div className="col-8">
        <input id="text" name="text" placeholder="" className="form-control here" required="required" type="text" />
      </div>
    </div>
    <div className="form-group row">
      <label for="email" className="col-4 col-form-label"> *</label> 
      <div className="col-8">
        <input id="email" name="email" placeholder=" " className="form-control here" required="required" type="text" />
      </div>
    </div>
    <div className="form-group row">
      <label for="website" className="col-4 col-form-label"></label> 
      <div className="col-8">
        <input id="website" name="website" placeholder="" className="form-control here" type="text" />
      </div>
    </div>
    <div className="form-group row">
      <label for="publicinfo" className="col-4 col-form-label"> </label> 
      <div className="col-8">
        <textarea id="publicinfo" name="publicinfo" cols="40" rows="4" className="form-control"></textarea>
      </div>
    </div>
    <div className="form-group row">
      <label for="newpass" className="col-4 col-form-label">  </label> 
      <div className="col-8">
        <input id="newpass" name="newpass" placeholder="  " className="form-control here" type="text" />
      </div>
    </div> 
    <div className="form-group row">
      <div className="offset-4 col-8">
        <button name="submit" type="submit" className="btn btn-primary">  </button>
      </div>
    </div>
   </form>
        
    );
  }
}



export default Profile;



